class Card extends React.Component {
  constructor(props){
    super(props);
  }

  render() {
    return (
      <div className = {"card " + this.props.animClass}>
        <a href = {this.props.link} target="_blank">
          <h2>{this.props.header}</h2>
          <img src = {this.props.image}/>
          <p>{this.props.text}</p>
        </a>
      </div>
    )
  }
}

class HobbiesTile extends React.Component {
  constructor(props){
    super(props);
  }

  render() {
    return (
      <div className = "tiles hobbies-tile">
        <div className = "carousel-message">
          <p>I like spending time with my family and traveling together, too.</p>
        </div>
        <MyCarousel/>
        <div className = "carousel-message">
          <p>And I'm always looking for more to do :)</p>
        </div>
      </div>
    )
  }
}

/**
 * A carousel
 * Will hold the quirky images
 */
class MyCarousel extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      justLoaded: true,
      currentSlide: 0,
      numImages: 5,
    }
    this.nextSlide = this.nextSlide.bind(this);
    this.prevSlide = this.prevSlide.bind(this);
  }
  myImages = [
    family, seattleMtn, ferris, museum, leagueback
  ];

  myCaptions = [
    "A picture with me and my family",
    "On a trip to Seattle, we went hiking in the mountains",
    "Also from Seattle -- the ferris wheel was amazing",
    "We went to see the JFK Museum",
    "At home, I connect with friends over League of Legends"
  ];

  myImageLinks = [

  ]

  /**
   * Called when user clicks on right arrow. 
   * Will move carousel to image that follows the image currently shown
   * (as dictated by their index in the myImages array)
   */
  nextSlide() {
    // console.log("next called");

    var newState = {
      justLoaded: false, 
      leftPressed: false
    };
    var slide = this.state.currentSlide + 1;
    if(slide === this.state.numImages){
      newState.currentSlide = 0;
    }
    else{
      newState.currentSlide = slide;
    }
    this.setState(newState);
  }

  
  /**
   * Called when user clicks on left arrow. 
   * Will move carousel to image that precedes the image currently shown
   * (as dictated by their index in the myImages array)
   */
  prevSlide() {
    // console.log("prev called");

    var newState = {
      justLoaded: false,
      leftPressed: true
    };
    var slide = this.state.currentSlide - 1;
    if(slide < 0){
      newState.currentSlide = this.state.numImages - 1;
    }
    
    else{
      newState.currentSlide = slide;
    }
    this.setState(newState);
  }

  /**
   * A function to do some conditional logic to pick the right class
   * for the image in question
   * @param index The index of the image being considered
   */
  pickClass(index) {
    var relativeSlide = index-this.state.currentSlide;
    if(relativeSlide === 0){
      if(this.state.leftPressed){
        return "active-left";
      }
      else{
        return "active-right";
      }
    }

    switch(index) {
      case 0: {
        if(this.state.leftPressed){
          if(relativeSlide === -1){
            return "standby-left";
          }
          else if(relativeSlide === (1-this.state.numImages)){
            return "inactive-left";
          }
          else{
            return "hidden";
          }
        }
        else{
          if(relativeSlide === -1){
            return "inactive-right";
          }
          else if(relativeSlide === (1-this.state.numImages)){
            return "standby-right";
          }
          else {
            return "hidden";
          }
        }
      }
      case (this.state.numImages-1): {
        if(this.state.leftPressed){
          if(relativeSlide === (this.state.numImages-1)){
            return "standby-left";
          }
          else if(relativeSlide === 1){
            return "inactive-left";
          }
          else {
            return "hidden";
          }
        }
        else{
          if(relativeSlide === (this.state.numImages-1)){
            return "inactive-right";
          }
          else if(relativeSlide === 1){
            return "standby-right";
          }
          else {
            return "hidden";
          }
        }
      }
      default: {
        if(this.state.leftPressed){
          if(relativeSlide === -1){
            return "standby-left";
          }
          else if(relativeSlide === 1){
            return "inactive-left";
          }
          else {
            return "hidden";
          }
        }
        else{
          if(relativeSlide === -1){
            return "inactive-right";
          }
          else if(relativeSlide === 1){
            return "standby-right";
          }
          else {
            return "hidden";
          }
        }
      }
    }
  }

  
  /**
   * A method specifically for when the user has just loaded the page
   * Ensures that there isn't any sort of animation while the user 
   * hasn't even interacted with the carousel
   */
  startingCarousel() {
    return (
      this.myImages.map((value,index) => (
        <CarouselImage
        key = {index}
        value = {value}
        caption = {this.myCaptions[index]}
        className = {
          (index === 0) ? "start active-left" :
          (index === 1) ? "start inactive-left" : 
          (index === (this.state.numImages-1) ) ? "standby-left" : "hidden"
        }/>
      ))  
    )
  }

  /**
   * Given the way I set up the carousel to manage both the next and the previous image, 
   * it's easier to simply create a separate method to avoid having the 'next' and 'prev' collide
   */
  twoImageCase() {
    return (
      this.myImages.map((value, index) => (
        <CarouselImage
        key = {index} 
        value = {value} 
        caption = {this.myCaptions[index]}
        className = { 
          ((index === this.state.currentSlide) ? 
          (this.state.leftPressed ? "active-left": "active-right") : 
          (this.state.leftPressed ? "inactive-left" : "inactive-right"))}/>
      ))
    )
  }

  /**
   * General case of the image createor function. Calls pickClass
   */
  generalCase() {
    return (
      this.myImages.map((value, index) => (
        <CarouselImage
        key = {index} 
        value = {value} 
        caption = {this.myCaptions[index]}
        className = {this.pickClass(index)}/>
      ))
    )
  }
  
  render() {
    return (
      // style = {{backgroundImage: `url(${this.myImages[this.state.currentSlide]})`}} 
        <div onContextMenu = {(event) => {event.preventDefault()}}  className = "carousel-wrapper" id="hobbies-carousel">
          <div className = "arrow-wrapper"  id="left-arrow-wrapper" onClick = {this.prevSlide}><i className="material-icons-round arrow-link" id="left-arrow">arrow_back</i></div>
          <div className = "arrow-wrapper"  id="right-arrow-wrapper" onClick = {this.nextSlide}><i className="material-icons-round arrow-link" id="right-arrow">arrow_forward</i></div>
          <div className = "clear-float"/>
          {
            (this.state.justLoaded) ? this.startingCarousel() :   
            (this.state.numImages === 2) ? this.twoImageCase() :
            this.generalCase()
          }
        </div>
    )
  }
}

class CarouselImage extends React.Component {
  constructor(props){
    super(props);
  }


  render(){
    return(
      <div className = {"carousel-img-wrapper " + this.props.className}>
        <img 
          src = {this.props.value} 
        />
        
        <div className = "image-p-wrapper">
          <p className = "image-p"> 
            {this.props.caption}
          </p>
        </div>
      </div>
    )
  }
}
class MainPage extends React.Component{ //Bundle all of these components to make routing easier
  constructor(props){
    super(props);
    this.state = {
      scrolly: 0,
      pageWidth: window.innerWidth,
      pageHeight: window.outerHeight,
      shouldAnimateStudyTile: false,
      shouldAnimateActivitiesTile: false,
      schoolTileAnimateClass: "",
      activitiesTileAnimateClass: "",
    };

    this.updateHeight = this.updateHeight.bind(this);
    this.updateWidth = this.updateWidth.bind(this);
    window.onscroll = this.updateHeight;
  }

  componentDidMount() {
    this.updateWidth();
    window.addEventListener('resize', this.updateWidth);
  }

  componentWillUnmount(){
    window.removeEventListener('resize', this.updateWidth);
  }

  constants = {
    studyTileThreshold: 140,
    activitiesTileThreshold: 750,
  };

  updateWidth(){
    this.setState({pageWidth: window.innerWidth})
  }

  updateHeight(){
    var scrollY = window.scrollY;
    // console.log(scrollY);

    var newState = {
      scrolly: window.scrollY,
    };

    if(!this.state.shouldAnimateStudyTile && scrollY >= this.constants.studyTileThreshold){
      newState.shouldAnimateStudyTile = true; //new state is a dictionary with key value pairs
      newState.schoolTileAnimateClass = "schoolTileAnimate"; //just try to access them and assign them as needed
    }

    if(!this.state.shouldAnimateActivitiesTile && scrollY >= this.constants.activitiesTileThreshold){
      newState.shouldAnimateActivitiesTile = true;
      newState.activitiesTileAnimateClass = "activitiesTileAnimate";
    }


    this.setState(newState);
  }
  render() {
    return (
      <div className = "mainPage-wrapper">
        <TitleName scrolly = {this.state.scrolly}   screenWidth = {this.state.pageWidth}/>
        <SchoolTile scrolly = {this.state.scrolly} schoolAnimClass = {this.state.schoolTileAnimateClass}/>
        <ActivitiesTile scrolly = {this.state.scrolly} activitiesAnimClass = {this.state.activitiesTileAnimateClass}/>
        <HobbiesTile scrolly = {this.state.scrolly} hobbiesAnimClass = {this.state.hobbiesTileAnimateClass}/>
      </div>
    );
  };
}

export default MainPage;